import Vue from 'vue'
import Router from 'vue-router'
import index from "@/pages/index"
import detail from "@/pages/detail/detail"
import list from "@/pages/list/list"
import register from "@/pages/register/register"
import login from "@/pages/login/login"
import catelist from "@/pages/index/components/catelist/catelist"
import mine from "@/pages/index/components/mine/mine"
import home from "@/pages/index/components/home/home"
import shop from "@/pages/index/components/shop/shop"

Vue.use(Router)

export default new Router({
    routes: [{
            path: "/index",
            component: index,
            children: [{
                    path: "home",
                    component: home
                },
                {
                    path: "catelist",
                    name: "",
                    component: catelist
                },
                {
                    path: "shop",
                    name: "",
                    component: shop
                },
                {
                    path: "mine",
                    name: "",
                    component: mine
                },
                {
                    path: "",
                    redirect: "home"
                }
            ]
        },
        {
            path: "/login",
            name: "",
            component: login
        },
        {
            path: "/list/:id",
            component: list
        },
        {
            path: "/register",
            name: "",
            component: register
        },
        {
            path: "/detail/:id",
            name: "",
            component: detail
        },
        {
            path: "*",
            redirect: "/login"
        }
    ]
})


