import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex);
const state = {
    isshow_back: false,
    isshow_register: false,
    zhong_content: ""
}
const getters = {
    gengxin_isshow_back() {
        return state.isshow_back
    },
    gengxin_isshow_register() {
        return state.isshow_register
    },
    gengxin_zhong_content() {
        return state.zhong_content
    },
}
const store = new Vuex.Store({
    state,
    getters
})

export default store


