<template>
  <div>
    <v-header></v-header>
    <div class="catelist">
      <div>
        <p
          v-for="(item,index) in json"
          :key="item.id"
          :class="index==n?'active':''"
          @click="n=index"
        >{{item.catename}}</p>
      </div>
      <div v-if="json.length != 0">
        <div v-for="item in json[n].children" :key="item.id">
          <div @click="fn1(item.id,item.catename)">
            <img :src="$pre+item.img" />
            <p>{{item.catename}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import req from "@/utils/http";
import Vue from "vue"
import {
    Toast
} from "vant";
Vue.use(Toast);
export default {
  mounted() {
    this.$store.state.zhong_content = this.$route.name;
    req({
      url: "/api/getcatetree",
      _this:this
    }).then(res => {
      this.json = res.data.list;
    });
  },
  data() {
    return {
      json: [],
      n: 0
    };
  },
  methods: {
    fn1(a,b) {
      this.$router.push("/list/" + a+"?name="+b);
    }
  }
};
</script>

<style scoped>
.catelist {
  display: flex;
  background-color: #fafafa;
  min-height: calc(100vh - 1rem - 0.8rem);
}
.catelist > div:nth-of-type(1) {
  flex: 1;
  /* border-right: 1px solid; */
}
.catelist > div:nth-of-type(1) > p {
  width: 100%;
  text-align: center;
  height: 1rem;
  line-height: 1rem;
}
.catelist > div:nth-of-type(1) > p.active {
  color: #ffaa00;
  position: relative;
  background-color: white;
}
.catelist > div:nth-of-type(1) > p.active::before {
  content: "";
  display: inline-block;
  width: 0.1rem;
  height: 100%;
  position: absolute;
  background-color: #1d84a7;
  left: 0;
  top: 0;
}
.catelist > div:nth-of-type(2) {
  flex: 2;
  height: 0;
  display: flex;
  flex-wrap: wrap;
}
.catelist > div:nth-of-type(2) > div {
  width: 50%;
  padding: 0.1rem 0.2rem;
}
.catelist > div:nth-of-type(2) > div > div {
  width: 2rem;
  height: 2rem;
  position: relative;
  border: 1px solid #1d84a7;
  border-radius: 0.1rem;
  overflow: hidden;
}
.catelist > div:nth-of-type(2) > div > div > img {
  width: 100%;
  height: 100%;
}
.catelist > div:nth-of-type(2) > div > div > p {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #1d84a7;
  text-align: center;
  color: white;
}
</style>


