<template>
  <div class="nav">
    <div>
      <div
        @click="fn1(index)"
        :class="index==n?'active':''"
        v-for="(item,index) in arr"
        :key="item.id"
      >
        <div>
          <img :src="item.img2" v-if="index==n" />
          <img :src="item.img1" v-else />
        </div>
        <p>{{item.name}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import tab_home_nor from "../assets/img/tab_home_nor.png";
import tab_home_hig from "../assets/img/tab_home_hig.png";
import tab_me_nor from "../assets/img/tab_me_nor.png";
import tab_me_hig from "../assets/img/tab_me_hig.png";
import tab_menu_nor from "../assets/img/tab_menu_nor.png";
import tab_menu_hig from "../assets/img/tab_menu_hig.png";
import tab_shopping_nor from "../assets/img/tab_shopping_nor.png";
import tab_shopping_hig from "../assets/img/tab_shopping_hig.png";
export default {
  mounted() {
    this.arr.forEach(item => {
      if (item.path == this.$route.path) {
        this.n = item.id - 1;
      }
    });
  },
  data() {
    return {
      n: 0,
      arr: [
        {
          id: 1,
          img1: tab_home_nor,
          img2: tab_home_hig,
          path: "/index/home",
          name: ""
        },
        {
          id: 2,
          img1: tab_menu_nor,
          img2: tab_menu_hig,
          path: "/index/catelist",
          name: ""
        },
        {
          id: 3,
          img1: tab_shopping_nor,
          img2: tab_shopping_hig,
          path: "/index/shop",
          name: ""
        },
        {
          id: 4,
          img1: tab_me_nor,
          img2: tab_me_hig,
          path: "/index/mine",
          name: ""
        }
      ]
    };
  },
  methods: {
    fn1(a) {
      if(this.n == a){
        return
      }
      this.n = a;
      this.$router.push(this.arr[a].path);
    }
  }
};
</script>

<style scoped lang="stylus">
@import '../stylus/index.styl';

.nav {
  width: 100%;
  height: 1rem;
}

.nav > div {
  width: 100%;
  height: 1rem;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  background-color: white;
  border-top:1px solid #ccc;
}

.nav > div > div {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  line-height: 1rem;
  font-size: 0.3rem;
  text-align: center;
}

.nav > div > div>p {
  // font-size: 0.3rem;
  line-height: 1;
  margin-top: 0.1rem;
}

.nav > div > div>div {
  line-height: 1;
}

.nav > div > div>div>img {
  width: 0.4rem;
}

.active {
  color: $f-color;
}

.nav > div > div:nth-of-type(1) {
  border: none;
}
</style>


