<template>
  <div class="header">
    <div>
      <p @click="$router.go(-1)">{{$store.state.isshow_back?"":""}}</p>
      <p>{{$store.state.zhong_content}}</p>
      <p @click="$router.push('/register')">{{$store.state.isshow_register?"":""}}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  }
};
</script>

<style scoped lang="stylus">
@import '../stylus/index.styl';

.header {
  height: 0.8rem;
  width: 100vw;
  font-size: 0.3rem;
  color: white;
}

.header > div {
  position: fixed;
  height: 0.8rem;
  width: 100vw;
  background-color: $f-color;
  display: flex;
  z-index: 999;
}

.header > div>P {
  flex: 1;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.35rem;
}
</style>


