import Vue from "vue"
import axios from "axios"
import qs from "qs"
import {
    Toast
} from "vant";
Vue.use(Toast);

let str = "/api"
Vue.prototype.$pre = "http://localhost:3000"
str = ""
Vue.prototype.$pre = ""
let hshu = (res, obj) => {
    if (res.data.code == 403) {
        Toast(res.data.msg)
        setTimeout(() => {
            obj._this.$router.push("/login");
        }, 2500);
    } else if (res.data.code != 200) {
        obj.isshow ? Toast(res.data.msg) : ""
    } else {
        obj.isshow ? Toast(res.data.msg) : ""
    }
    return res
}
export default (obj = {
    url,
    method,
    data,
    isshow,
    _this
}) => {
    if (obj.method == "get" || obj.method == undefined) {
        return localStorage.getItem("user") == null ? axios({
            url: str + obj.url,
            method: obj.method,
            params: obj.data
        }).then(res => hshu(res, obj)) : axios({
            url: str + obj.url,
            method: obj.method,
            params: obj.data,
            headers: {
                authorization: JSON.parse(localStorage.getItem("user")).token
            }
        }).then(res => hshu(res, obj))
    } else if (obj.method == "post") {
        return localStorage.getItem("user") == null ? axios({
            url: str + obj.url,
            method: obj.method,
            data: qs.stringify(obj.data)
        }).then(res => hshu(res, obj)) : axios({
            url: str + obj.url,
            method: obj.method,
            data: qs.stringify(obj.data),
            headers: {
                authorization: JSON.parse(localStorage.getItem("user")).token
            }
        }).then(res => hshu(res, obj))
    }
}


