<template>
  <div class="detail">
    <v-header></v-header>
    <div>
      <div>
        <img :src="$pre+json.img" style="width:100vw;height:5rem" />
      </div>
      <div>
        <div>
          <p>{{json.goodsname}}</p>
          <div>
            <div>
              <img :src="img.cart" />
            </div>
            <p></p>
          </div>
        </div>
        <div>
          <span>{{json.price}}</span>
          <span v-if="json.ishot == 1"></span>
          <span v-if="json.isnew == 1"></span>
        </div>
        <p style="text-decoration:line-through">{{json.market_price}}</p>
      </div>
      <hr />
      <div v-html="json.description"></div>
    </div>
    <div>
      <div>
        <div @click="showPopup"></div>
      </div>
    </div>
    <van-popup v-model="show" position="bottom">
      <div class="content">
        <div>
          <img :src="$pre+json.img" alt />
          <p>{{json.goodsname}}</p>
        </div>
        <div>
          <h2>{{json.specsname}}</h2>
          <div>
            <span style="margin-bottom:.1rem"
              v-for="(item,index) in json.specsattr"
              :key="index"
              :class="index == n?'active':''"
              @click="n=index"
            >{{item}}</span>
          </div>
        </div>
        <div @click="fn1(json.id)"></div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import req from "@/utils/http";
import cart from "../../assets/img/img/cart.png";
import Vue from "vue";
import { Popup } from "vant";

Vue.use(Popup);
export default {
  mounted() {
    this.$store.state.isshow_back = true;
    this.$store.state.isshow_register = false;
    this.$store.state.zhong_content = this.$route.name;
    req({
      url: "/api/getgoodsinfo",
      data: {
        id: this.$route.params.id
      },
      _this:this
    }).then(res => {
      res.data.list[0].specsattr = JSON.parse(res.data.list[0].specsattr);
      this.json = res.data.list[0];
    });
  },
  data() {
    return {
      json: {},
      img: {
        cart
      },
      show: false,
      n: 0
    };
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    fn1(id) {
      req({
        url: "/api/cartadd",
        method: "post",
        data: {
          goodsid: id,
          num:1,
          uid:JSON.parse(localStorage.getItem("user")).uid
        },
        isshow:true
      }).then(res => {
        this.show = false;
      });
    }
  }
};
</script>

<style scoped>
/* @import "../../stylus/index.styl"; */
hr {
  height: 0.3rem;
  background-color: #ccc;
}
.detail > div:nth-of-type(2) > div:nth-of-type(2) > div:nth-of-type(1) {
  display: flex;
  justify-content: space-between;
  height: 1rem;
  align-items: center;
  padding: 0 0.3rem;
}
.detail > div:nth-of-type(2) > div:nth-of-type(2) > div:nth-of-type(1) > p {
  font-size: 0.4rem;
}
.detail > div:nth-of-type(2) > div:nth-of-type(2) > div:nth-of-type(1) > div {
  border-left: 0.05rem solid #eee;
  font-size: 0.25rem;
  padding-left: 0.1rem;
}
.detail
  > div:nth-of-type(2)
  > div:nth-of-type(2)
  > div:nth-of-type(1)
  > div
  > div {
  display: flex;
  justify-content: center;
  margin-bottom: 0.1rem;
}
.detail
  > div:nth-of-type(2)
  > div:nth-of-type(2)
  > div:nth-of-type(1)
  > div
  img {
  width: 0.3rem;
}
.detail > div:nth-of-type(2) > div:nth-of-type(2) > div:nth-of-type(2) {
  padding-left: 0.3rem;
  font-size: 0.3rem;
  color: #ffaa00;
}
.detail
  > div:nth-of-type(2)
  > div:nth-of-type(2)
  > div:nth-of-type(2)
  > span:nth-of-type(1) {
  color: #ff0000;
  margin-right: 0.3rem;
}
.detail
  > div:nth-of-type(2)
  > div:nth-of-type(2)
  > div:nth-of-type(2)
  > span:nth-of-type(2),
.detail
  > div:nth-of-type(2)
  > div:nth-of-type(2)
  > div:nth-of-type(2)
  > span:nth-of-type(3) {
  display: inline-block;
  width: 1rem;
  height: 0.5rem;
  border: 1px solid #ffaa00;
  text-align: center;
  line-height: 0.5rem;
}
.detail > div:nth-of-type(2) > div:nth-of-type(2) > p {
  text-indent: 0.25rem;
}

.detail > div:nth-of-type(3) {
  height: 1rem;
  width: 100vw;
}
.detail > div:nth-of-type(3) > div {
  display: flex;
  background-color: white;
  justify-content: flex-end;
  height: 1rem;
  width: 100vw;
  position: fixed;
  bottom: 0;
  left: 0;
}
.detail > div:nth-of-type(3) > div > div {
  line-height: 1rem;
  text-align: center;
  width: 2rem;
  color: white;
  background-color: #ffaa00;
}
.content {
  padding: 0.3rem;
}
.content > div:nth-of-type(1) {
  display: flex;
}
.content > div:nth-of-type(1) > img {
  width: 2rem;
  border-radius: 0.1rem;
}
.content > div:nth-of-type(1) > p {
  font-size: 0.45rem;
  margin-left: 0.2rem;
}
.content > div:nth-of-type(2) > h2 {
  font-weight: bold;
  line-height: 2;
}
.content > div:nth-of-type(2) > div {
  /* margin-left: -.3rem; */
  font-size: 0.4rem;
}
.content > div:nth-of-type(2) > div > span {
  display: inline-block;
  padding: 0 0.3rem;
  line-height: 2;
  border: 1px solid;
  border-radius: 0.1rem;
  text-align: center;
  margin: 0 0.3rem;
}
.content > div:nth-of-type(2) > div > span.active {
  border-color: #ffaa00;
  color: #ffaa00;
}
.content > div:nth-of-type(3) {
  width: 2rem;
  height: 1rem;
  line-height: 1rem;
  background-color: #ffaa00;
  color: white;
  text-align: center;
  margin-left: 50%;
  transform: translate(-50%);
  margin-top: 0.3rem;
  /* margin-bottom: .5rem; */
}
</style>


