<template>
  <div>
    <div class="header">
      <div>
        <img :src="img.logo" alt="logo" />
      </div>
      <div>
        <input type="text" placeholder="" />
      </div>
    </div>
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in banner_lis" :key="item.id">
        <img :src="$pre+item.img" style="width:100vw;height:3rem" />
      </van-swipe-item>
    </van-swipe>
    <div class="hd_active">
      <a>
        <div>
          <img :src="img.xsqg" />
        </div>
        <p></p>
      </a>
      <a>
        <div>
          <img :src="img.xsqg" />
        </div>
        <p></p>
      </a>
      <a>
        <div>
          <img :src="img.xsqg" />
        </div>
        <p></p>
      </a>
      <a>
        <div>
          <img :src="img.xsqg" />
        </div>
        <p></p>
      </a>
    </div>
    <v-list :sp_lis="sp_lis"></v-list>
  </div>
</template>

<script>
import xsqg from "../../../../assets/img/img/home/1.jpg";
import logo from "../../../../assets/img/img/home/logo.jpg";
import Vue from "vue";
import { Swipe, SwipeItem } from "vant";
import req from "@/utils/http";

Vue.use(Swipe);
Vue.use(SwipeItem);
export default {
  data() {
    return {
      banner_lis: [],
      sp_lis: [],
      img: {
        xsqg,
        logo
      }
    };
  },
  mounted() {
    req({
      url: "/api/bannerlist",
      _this:this
    }).then(res => {
      this.banner_lis = res.data.list;
    });
    req({
      url: "/api/goodslist",
      data: {
        size: 5,
        page: 1
      },
      _this:this
    }).then(res => {
      this.sp_lis = res.data.list;
    });
  }
};
</script>

<style scoped>
.hd_active {
  display: flex;
  /* justify-content: space-around; */
}
.hd_active > a {
  flex: 1;
  margin: 0 0.1rem;
}
.hd_active > a > div > img {
  width: 100%;
}
.hd_active > a > p {
  font-size: 0.2rem;
  text-align: center;
  margin-top: 0.1rem;
}
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
}
.header {
  display: flex;
  height: 1.4rem;
  align-items: center;
  padding-left: 0.3rem;
}
.header > div:nth-of-type(1) {
  flex: 1;
}
.header > div:nth-of-type(1) > img {
  width: 100%;
}
.header > div:nth-of-type(2) {
  flex: 2;
  display: flex;
  justify-content: center;
}
.header > div:nth-of-type(2) > input {
  background-color: #ccc;
  border-radius: 0.2rem;
  text-indent: 0.2rem;
  height: 0.5rem;
  width: 35%;
  font-size: 0.2rem;
}
</style>


