<template>
  <div class="register">
    <v-header></v-header>
    <div class="from">
      <div>
        
        <input type="text" v-model="user.phone" />
      </div>
      <div>
        
        <input type="text" v-model="user.nickname" />
      </div>
      <div>
        
        <input type="text" v-model="user.password" />
      </div>
      <button @click="register"></button>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import req from "@/utils/http";
export default {
  mounted() {
    this.$store.state.isshow_back = true;
    this.$store.state.isshow_register = false;
    this.$store.state.zhong_content = this.$route.name;
  },
  data() {
    return {
      user: {
        phone: "",
        password: "",
        nickname: ""
      }
    };
  },
  methods: {
    register() {
      req({
        url: "/api/register",
        method: "post",
        data: this.user,
        isshow:true
      }).then(res => {
        setTimeout(() => {
          this.$router.push("/login");
        }, 2500);
      });
    }
  }
};
</script>

<style scoped lang="stylus">
@import "../../stylus/index.styl";
.register {
  height: 100vh;
  position: relative;
}
.register > .from {
  width: 80%;
  height: 3rem;
  position: absolute;
  top: 3rem;
  left: 50%;
  transform: translate(-50%);
}
.register > .from > div {
  margin-bottom: 0.5rem;
}
.register > .from > div > input {
  width: 100%;
  border-bottom: 1px solid #a3a3a3;
  display: inline-block;
  height: 0.8rem;
}
.register > .from > button {
  width: 100%;
  background-color: $f-color;
  line-height: 0.8rem;
  color: white;
  border-radius: 0.1rem;
}
</style>


