<template>
  <div>
    <div class="mine">
      <div class="header">
        <div>
          <img :src="img.set" />
          <p>{{this.$route.name}}</p>
          <img :src="img.news" />
        </div>
      </div>
      <div class="zwei">
        <div class="touxiang">
          <img :src="img.toux" />
          <p>{{name}}</p>
        </div>
      </div>
      <div class="sc">
        <img :src="img.keep" />
        ({{5}})
      </div>
      <div class="wode">
        <p></p>
        <p></p>
      </div>
      <div class="middle_nav">
        <div>
          <img :src="img.icon_refund" />
          <p></p>
        </div>
        <div>
          <img :src="img.icon_refund" />
          <p></p>
        </div>
        <div>
          <img :src="img.icon_refund" />
          <p></p>
        </div>
        <div>
          <img :src="img.icon_refund" />
          <p></p>
        </div>
        <div>
          <img :src="img.icon_refund" />
          <p></p>
        </div>
      </div>
      <div class="sdao"></div>
    </div>
  </div>
</template>

<script>
import set from "../../../../assets/img/set.png";
import news from "../../../../assets/img/news.png";
import toux from "../../../../assets/img/1.jpg";
import keep from "../../../../assets/img/keep.png";
import icon_refund from "../../../../assets/img/icon_refund.png";
export default {
  data() {
    return {
      img: {
        set,
        news,
        toux,
        keep,
        icon_refund
      },
      name:JSON.parse(localStorage.getItem("user")).nickname
    };
  }
};
</script>

<style scoped>
.mine{
  min-height: calc(100vh - 1rem);
  background-color: #fafafa;
}
.header {
  background-color: #ff9900;
  height: 3rem;
  width: 100vw;
}
.header > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.4rem;
  color: white;
  padding: 0.1rem 0.3rem;
}
.header > div > img {
  width: 0.5rem;
  height: 0.5rem;
}
.zwei {
  height: 2rem;
  width: 100vw;
  background-color: white;
}
.touxiang {
  position: absolute;
  width: 2.2rem;
  height: 2.2rem;
  background-color: white;
  border-radius: 50%;
  left: 50%;
  transform: translate(-50%);
  top: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.touxiang > img {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.touxiang > p {
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: -0.4rem;
  left: 50%;
  transform: translate(-50%);
  font-size: 0.28rem;
}
.sc {
  height: 1rem;
  display: flex;
  align-items: center;
  font-size: 0.4rem;
  background-color: white;
  margin-bottom: .1rem;
}
.sc > img {
  height: 0.4rem;
  vertical-align: middle;
  margin: 0 0.3rem;
}
.wode {
  display: flex;
  justify-content: space-between;
  font-size: 0.35rem;
  height: 1rem;
  border-bottom: 1px solid;
  align-items: center;
  padding-left: 1rem;
  background-color: white;
}
.wode > p:nth-of-type(2) {
  color: #999;
}
.middle_nav {
  display: flex;
  justify-content: space-around;
  height: 1.8rem;
  align-items: center;
  margin-bottom: .1rem;
  background-color: white;
}
.middle_nav > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.middle_nav > div > img {
  width: 0.7rem;
}
.sdao {
  font-size: 0.35rem;
  color: #999;
  height: 1rem;
  line-height: 1rem;
  padding-left: .5rem;
  background-color: white;

}
.mine{
  background-color: #fafafa;
  height: calc(100% - 1rem);
}
</style>


