<template>
  <div>
    <v-header></v-header>
    <div class="gw_cart" v-if="json.length != 0">
      <div v-for="(item,index) in json" :key="item.id">
        <p>
          <img :src="img[4]" /> 
        </p>
        <div :class="isshan?'active':''">
          <div class="card">
            <div @click="fn2(index)">
              <img :src="item.ischecked?img[2]:img[3]" />
            </div>
            <div>
              <img :src="$pre+item.img" />
            </div>
            <div>
              <h3>{{item.goodsname}}</h3>
              <div>
                <span @click="jian(item.goodsid,item.id)">-</span>
                <span>{{item.num}}</span>
                <span @click="add(item.goodsid,item.id)">+</span>
              </div>
              <p>{{(item.price*item.num)|tianzero}}</p>
            </div>
            <div>{{item.price|tianzero}}</div>
          </div>
          <div @click="fn3(item.id,index)"></div>
        </div>
      </div>
    </div>
    <div class="fuqian">
      <div>
        <div @click="fn1">
          <img :src="isshow?img[2]:img[3]" />
          <p></p>
        </div>
        <div @click="isshan = !isshan">
          <img :src="isshan?img[0]:img[1]" />
          <p></p>
        </div>
        <div>
          <p>{{sum|tianzero}}</p>
          <p>()</p>
        </div>
        <div></div>
      </div>
    </div>
  </div>
</template>

<script>
import editor_hig from "../../../../assets/img/editor_hig.png";
import editor_nor from "../../../../assets/img/editor_nor.png";
import radio_hig from "../../../../assets/img/radio_hig.png";
import radio_nor from "../../../../assets/img/radio_nor.png";
import store from "../../../../assets/img/store.png";
import req from "@/utils/http";
import { Dialog } from "vant";
export default {
  mounted() {
    this.$store.state.isshow_back = false;
    this.$store.state.isshow_register = false;
    this.$store.state.zhong_content = this.$route.name;
    let _this = this;
    this.request({ _this });
  },
  data() {
    return {
      img: [editor_hig, editor_nor, radio_hig, radio_nor, store],
      json: [],
      isshow: false,
      isshan: false,
      show: false,
      lock: true
    };
  },
  methods: {
    fn1() {
      this.isshow = !this.isshow;
      this.json.forEach(item => {
        item.ischecked = this.isshow;
      });
    },
    fn2(index) {
      this.json[index].ischecked = !this.json[index].ischecked;
      let a = this.json.every(item => {
        return item.ischecked;
      });
      this.isshow = a;
    },
    fn3(id, index) {
      this.show = true;
      Dialog.confirm({
        message: ""
      }).then(() => {
        req({
          url: "/api/cartdelete",
          method: "post",
          data: {
            id
          },
          isshow: true
        }).then(res => {
          if (res.data.code == 200) {
            this.json.forEach(item => {
              if (item.id == id) {
                this.json.splice(index, 1);
              }
            });
            if (this.json.length == 0) {
              this.isshow = false;
              return;
            }
          }
        });
      });
    },
    request(obj = { id, _this }) {
      req({
        url: "/api/cartlist",
        data: {
          uid: JSON.parse(localStorage.getItem("user")).uid
        },
        _this: obj._this
      }).then(res => {
        if (res.data.list == null) {
          this.json = [];
          return;
        }
        if (this.json.length == 0) {
          res.data.list.forEach(item => {
            item.ischecked = false;
          });
          this.json = res.data.list;
        } else {
          this.json.forEach((item, index) => {
            if (item.goodsid == obj.id) {
              let dx = this.json[index];
              dx.num = res.data.list[index].num;
              this.$set(this.json, index, dx);
              this.lock = true;
            }
          });
        }
      });
    },
    add(goodsid, id) {
      req({
        url: "/api/cartedit",
        method: "post",
        data: {
          id: id,
          type: 2
        },
        _this: this
      }).then(res => {
        this.request({ id: goodsid });
      });
    },
    jian(goodsid, id) {
      if (this.lock) {
        this.lock = false;
        let index = this.json.findIndex(item => {
          return item.goodsid == goodsid;
        });
        console.log(this.json[index].num);
        if (this.json[index].num <= 1) {
          this.json[index].num = 1;
        } else {
          req({
            url: "/api/cartedit",
            method: "post",
            data: {
              id: id,
              type: 1
            },
            _this: this
          }).then(res => {
            this.request({ id: goodsid });
          });
        }
      }
    }
  },
  computed: {
    sum: function() {
      let total = 0;
      this.json.forEach(item => {
        if (item.ischecked) {
          total += item.price * item.num;
        }
      });
      return total;
    }
  }
};
</script>

<style scoped>
.fuqian {
  width: 100vw;
  height: 1rem;
}
.fuqian > div {
  width: 100vw;
  height: 1.2rem;
  display: flex;
  position: fixed;
  left: 0;
  bottom: 1rem;
  background: white;
}
.fuqian > div > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.fuqian > div > div:nth-of-type(1) {
  flex: 1;
  color: #999;
}
.fuqian > div > div:nth-of-type(2) {
  flex: 1;
  color: #999;
}
.fuqian > div > div:nth-of-type(3) {
  flex: 2;
}
.fuqian > div > div:nth-of-type(3) > p {
  color: #666;
}
.fuqian > div > div:nth-of-type(3) > p:nth-of-type(2) {
  color: #999;
}
.fuqian > div > div:nth-of-type(4) {
  flex: 2;
  background-color: #ffaa00;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.4rem;
}
.gw_cart {
  width: 100vw;
  overflow: hidden;
}
.gw_cart > div > p {
  height: 0.8rem;
  line-height: 0.8rem;
  color: #ccc;
  padding-left: 0.3rem;
}
.gw_cart > div > p > img {
  vertical-align: middle;
}
.gw_cart > div > div {
  display: flex;
  flex-wrap: nowrap;
  width: 150%;
}
.gw_cart > div > div.active {
  margin-left: -1rem;
}
.gw_cart > div > div > .card {
  display: flex;
  width: 100vw;
  height: 2rem;
  /* margin-left: -1rem; */
}
.gw_cart > div > div > div:nth-of-type(2) {
  background-color: #ffaa00;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1rem;
}
.gw_cart > div > div > .card > div:nth-of-type(1) {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: center;
}
.gw_cart > div > div > .card > div:nth-of-type(2) {
  flex: 2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gw_cart > div > div > .card > div:nth-of-type(2) > img {
  width: 1.5rem;
  height: 1.5rem;
}
.gw_cart > div > div > .card > div:nth-of-type(3) {
  flex: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.gw_cart > div > div > .card > div:nth-of-type(3) > h3 {
  font-size: 0.3rem;
}
.gw_cart > div > div > .card > div:nth-of-type(3) > div {
  display: flex;
  justify-content: start;
}

.gw_cart > div > div > .card > div:nth-of-type(3) > div > span {
  display: inline-block;
  width: 0.8rem;
  height: 0.5rem;
  line-height: 0.5rem;
  border: 1px solid #ccc;
  text-align: center;
}
.gw_cart > div > div > .card > div:nth-of-type(4) {
  flex: 2;
  font-size: 0.3rem;
  margin-top: 0.2rem;
}
</style>


