<template>
  <ul class="list">
    <li v-for="item in sp_lis" :key="item.id">
      <div>
        <img :src="$pre+item.img" />
      </div>
      <div>
        <p v-text="item.goodsname"></p>
        <div>
          <p>{{item.price|tianzero}}</p>
          <p @click="fn1(item.id)"></p>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["sp_lis"],
  methods:{
    fn1(a){
      this.$router.push("/detail/"+a)
    }
  }
};
</script>

<style scoped>
.list {
  width: 100vw;
  /* height: 2rem; */
}
.list > li {
  display: flex;
  padding: 0.2rem;
}
.list > li > div:nth-of-type(2) {
  margin-left: 0.3rem;
}
.list > li > div > img {
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 0.1rem;
}
.list > li > div:nth-of-type(2){
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.list > li > div:nth-of-type(2)>p{
  font-size: .4rem;
}
.list > li > div:nth-of-type(2)>div>p:nth-of-type(1){
  color: red;
}
.list > li > div:nth-of-type(2)>div>p:nth-of-type(2){
  width: 2rem;
  height: .7rem;
  background-color: #ff9900;
  color: white;
  border-radius: .1rem;
  line-height: .7rem;
  text-align: center;
}
</style>


