<template>
  <div class="login">
    <v-header></v-header>
    <div class="from">
      <div>
        :
        <input type="text" v-model="user.phone" />
      </div>
      <div>
        :
        <input type="text" v-model="user.password" />
      </div>
      <p></p>
      <button @click="login"></button>
    </div>
  </div>
</template>

<script>
import req from "@/utils/http";
export default {
  mounted() {
    this.$store.state.isshow_back = false;
    this.$store.state.isshow_register = true;
    this.$store.state.zhong_content = this.$route.name;
  },
  methods: {
    login() {
      req({
        url: "/api/login",
        method: "post",
        data: this.user,
        isshow:true
      }).then(res => {
        localStorage.setItem("user", JSON.stringify(res.data.list));
        setTimeout(() => {
          this.$router.push("/index/home");
        }, 2500);
      });
    }
  },
  data() {
    return {
      user: {
        phone: "",
        password: ""
      }
    };
  }
};
</script>

<style scoped lang="stylus">
@import "../../stylus/index.styl";
.login {
  height: 100vh;
  position: relative;
}
.login > .from {
  width: 80%;
  height: 3rem;
  position: absolute;
  top: 3rem;
  left: 50%;
  transform: translate(-50%);
}
.login > .from > div:nth-of-type(1) {
  margin-bottom: 0.5rem;
}
.login > .from > div > input {
  width: 100%;
  border-bottom: 1px solid #a3a3a3;
  display: inline-block;
  height: 0.8rem;
}
.login > .from > p {
  text-align: right;
  line-height: 0.8rem;
  color: #999;
}
.login > .from > button {
  width: 100%;
  background-color: $f-color;
  line-height: 0.8rem;
  color: white;
  border-radius: 0.1rem;
}
</style>


