<template>
  <div>
    <v-header></v-header>
    <v-list :sp_lis="json"></v-list>
  </div>
</template>

<script>
import req from "@/utils/http";
export default {
  mounted() {
    this.$store.state.isshow_back = true;
    this.$store.state.isshow_register = false;
    this.$store.state.zhong_content = this.$route.query.name;
    req({
      url: "/api/getgoods",
      data: {
        fid: this.$route.params.id
      },
      _this:this
    }).then(res => {
      this.json = res.data.list;
    });
  },
  data() {
    return {
        json:[]
    };
  }
};
</script>
<style>
</style>


